const TestLength = () => {
    const vs = [[115, -32], [131, -22]];
    let len0 = 0;
    for(let i = 0 ; i < vs.length - 1; ++i) {
        const v0 = new NJSCORE.NVector3(vs[i][0], vs[i][1], 0);
        const v1 = new NJSCORE.NVector3(vs[i+1][0], vs[i+1][1], 0);
        const v = NJSCORE.NVector3.KMeterFromGPSPos(v0, v1)
        len0 += v;
    }

    let len1 = 0;
    for(let i = 0 ; i < vs.length - 1; ++i) {
        const v0 = new NJSCORE.NVector3(vs[i][0], vs[i][1], 0);
        const v1 = new NJSCORE.NVector3(vs[i+1][0], vs[i+1][1], 0);
        const v = distance([v0.x, v0.y], [v1.x, v1.y], UNIT.kilometers);
        len1 += v;
    }

    var line = turf.lineString(vs);
    var length = turf.length(line, { units: 'kilometers' });

    // console.log(length, 'len0', len0);
    // console.log(length, 'len1', len1);
};