export const earthRadius = 6371008.8;
export const UNIT = {
    centimeters: earthRadius * 100,
    degrees: 360 / (2 * Math.PI),
    feet: earthRadius * 3.28084,
    inches: earthRadius * 39.37,
    kilometers: earthRadius / 1000,
    meters: earthRadius,
    miles: earthRadius / 1609.344,
    millimeters: earthRadius * 1000,
    nauticalmiles: earthRadius / 1852,
    yards: earthRadius * 1.0936,
  };
export const degreesToRadians = (degrees: number): number => {
  return ((degrees % 360) * Math.PI) / 180;
};
export const distance = (coord0: number[], coord1: number[], unit = UNIT.meters ) => {
  const dLat = degreesToRadians(coord1[1] - coord0[1]);
  const dLon = degreesToRadians(coord1[0] - coord0[0]);
  const lat1 = degreesToRadians(coord0[1]);
  const lat2 = degreesToRadians(coord1[1]);
  const val = Math.pow(Math.sin(dLat / 2), 2) + Math.pow(Math.sin(dLon / 2), 2) * Math.cos(lat1) * Math.cos(lat2);
  return 2 * Math.atan2(Math.sqrt(val), Math.sqrt(1 - val)) * unit;
};